"use client";
import { useState, useEffect } from "react";
import { BiSearch } from "react-icons/bi";
import Image from "next/image";

export default function VolunteerRequestTable() {
  const [requests, setRequests] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [entriesPerPage, setEntriesPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    const fetchRequests = async () => {
      setLoading(true);
      setTimeout(() => {
        const dummyData = [
          {
            id: 1,
            name: "Aman Bhardwaj",
            dob: "2004-02-20",
            mobile: "8331952380",
            email: "bhardwaj04aman@gmail.com",
            nationality: "Indian",
            address: "Sion, Mumbai",
            occupation: "Doctor",
            designation: "Doctor",
            message: "I love spreading knowledge and happiness and believe that i can use my clinical knowledge for the benefit of the society along with my own growth!",
            identityProof: "/images/doctor1.jpg"
          }
        ];
        setRequests(dummyData);
        setLoading(false);
      }, 500);
    };

    fetchRequests();
  }, []);

  const filteredRequests = requests.filter(request =>
    request.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    request.email.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const indexOfLastEntry = currentPage * entriesPerPage;
  const indexOfFirstEntry = indexOfLastEntry - entriesPerPage;
  const currentEntries = filteredRequests.slice(indexOfFirstEntry, indexOfLastEntry);
  const totalPages = Math.ceil(filteredRequests.length / entriesPerPage);

  const handleActivate = (id) => {
    console.log("Activate:", id);
  };

  const handleBlock = (id) => {
    console.log("Block:", id);
  };

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Volunteer Request Data</h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li><a href="/admin" className="hover:underline text-gray-600 hover:text-blue-600">Home</a></li>
            <li className="text-gray-400">/</li>
            <li><a href="/admin/volunteer-requests" className="hover:underline text-gray-600 hover:text-blue-600">Volunteer Rqst</a></li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">Volunteer Request Data</li>
          </ol>
        </nav>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6">
        <h2 className="text-xl font-semibold text-blue-900 mb-6">Volunteer Request</h2>

        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-2">
            <select value={entriesPerPage} onChange={(e) => { setEntriesPerPage(Number(e.target.value)); setCurrentPage(1); }}
              className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500">
              <option value={10}>10</option>
              <option value={25}>25</option>
              <option value={50}>50</option>
            </select>
            <span className="text-sm text-green-600 font-medium">entries per page</span>
          </div>
          <div className="relative">
            <input type="text" placeholder="Search..." value={searchTerm} onChange={(e) => { setSearchTerm(e.target.value); setCurrentPage(1); }}
              className="border border-gray-300 rounded-lg pl-4 pr-10 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 w-64"/>
            <BiSearch className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={20} />
          </div>
        </div>

        {loading ? (
          <div className="text-center py-12">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500 mx-auto"></div>
          </div>
        ) : (
          <>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="bg-gray-50 border-b border-gray-200">
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Name</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">DOB</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Mob No.</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Email</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Nationality</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">C.Address</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Occupation</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Designation</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Message</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Identity Proof</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Action</th>
                  </tr>
                </thead>
                <tbody>
                  {currentEntries.map((request, index) => (
                    <tr key={request.id} className={`border-b border-gray-100 hover:bg-gray-50 transition-colors ${index % 2 === 0 ? 'bg-white' : 'bg-gray-50/50'}`}>
                      <td className="px-4 py-3 text-sm text-gray-900">{request.name}</td>
                      <td className="px-4 py-3 text-sm text-gray-700">{request.dob}</td>
                      <td className="px-4 py-3 text-sm text-gray-700">{request.mobile}</td>
                      <td className="px-4 py-3 text-sm text-gray-700">{request.email}</td>
                      <td className="px-4 py-3 text-sm text-gray-700">{request.nationality}</td>
                      <td className="px-4 py-3 text-sm text-gray-700">{request.address}</td>
                      <td className="px-4 py-3 text-sm text-gray-700">{request.occupation}</td>
                      <td className="px-4 py-3 text-sm text-gray-700">{request.designation}</td>
                      <td className="px-4 py-3 text-sm text-gray-700 max-w-xs"><div className="line-clamp-2">{request.message}</div></td>
                      <td className="px-4 py-3">
                        <div className="w-20 h-20 relative rounded-lg overflow-hidden border border-gray-200">
                          <Image src={request.identityProof} alt="ID Proof" fill className="object-cover"/>
                        </div>
                      </td>
                      <td className="px-4 py-3">
                        <div className="flex flex-col gap-2">
                          <button onClick={() => handleActivate(request.id)} className="px-4 py-2 bg-green-600 text-white text-sm font-medium rounded-lg hover:bg-green-700 transition-colors">Activate</button>
                          <button onClick={() => handleBlock(request.id)} className="px-4 py-2 bg-red-600 text-white text-sm font-medium rounded-lg hover:bg-red-700 transition-colors">Block</button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </>
        )}
      </div>
    </>
  );
}



